<template>
    <div>
        <div class="kd-page-title">信息确认</div>
        <div class="kd-page-content-wrapper kd-page-padding wrap-info">
            <text-table style="height: 80%" :data="getDataArr()"/>
            <div>临时身份证制证周期为三个工作日，选择自取领取方式，待收到短信通知后至指定地点领取。</div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button icon="kedaIcon-circleright" text="提交" :button-handle="nextStep"/>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { translateLicense } from '@/utils/tools';
import { getOrderDetail, processInformationUpload } from '@/service/human/identify-card';
import textTable from '@/components/text-table';
// import { playAudio } from '@/utils/media';

export default {
    name: 'temporary-id-card-personal',
    computed: {
        ...mapState('temporaryCardHandle', {
            personalInfo: 'personalInfo',
            result: 'result'
        })
    },
    components: { textTable },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('temporaryCardHandle', ['setResult', 'setOrderDetail']),
        getDataArr () {
            return translateLicense(this.personalInfo);
        },
        async nextStep () {
            const res = await processInformationUpload();
            if (res && res.code === '0') {
                this.setResult(res.result);
                await this.getQR();
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        getQR () {
            return new Promise(resolve => {
                setTimeout(async () => {
                    const res = await getOrderDetail({
                        slh: this.result.slh
                    });// 获取订单详情
                    if (res && res.code === '0') {
                        this.setOrderDetail(res.result);
                        this.changeStep('down');
                    } else {
                        this.changeTipPage({ message: res.message, enableBack: true });
                    }
                    resolve();
                }, 8000);
            });
        }
    },
    mounted () {
        // this.audioCar = playAudio(audioCar);
        // this.audioCar.play();
    },
    beforeDestroy () {
        // this.audioCar.destroy();
    }
};
</script>

