<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')" :disable-read-card="true"/>
        <process :type="1" v-if="currentStep === steps.findIndex(step=>step.key === 'informationQuery')" :handleBusiness="informationQuery"/>
        <personal v-if="currentStep === steps.findIndex(step=>step.key === 'personal')"/>
        <unified-payment-choose v-if="currentStep === steps.findIndex(step=>step.key === 'payChoose')" :payQrCode="result.slh"/>
        <unified-payment-appear v-if="currentStep === steps.findIndex(step=>step.key === 'payResult')" :payQrCode="result.slh" :nextFunction="checkPayStatus"/>
        <process :type="2" v-if="currentStep === steps.findIndex(step=>step.key === 'tickPrint')" :handleBusiness="tickPrint" waitText="业务凭条打印中，请稍后......"/>
        <complete v-if="currentStep === steps.findIndex(step=>step.key === 'complete')" :data="result"/>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import personal from './components/personal';
import complete from '@/components/complete';
import process from '@/components/process';
import { processInformationQuery } from '@/service/human/identify-card';
import { tickPrint } from '@/service/HardwareRequest';
import { getIdNumberTick } from '@/constant/tickertape';
import unifiedPaymentChoose from '@/view/pageComponents/unified-payment-choose';
import unifiedPaymentAppear from '@/view/pageComponents/unified-payment-appear';
import { ID_NUMBER_TEMP } from '@/constant/stepsOption';
import { checkDriverPayStatus } from '@/service/traffic/driver/replacement';

export default {
    name: 'TemporaryIdCardIndex', // 临时身份证办理
    components: { identifyCardPage, complete, personal, process, unifiedPaymentChoose, unifiedPaymentAppear },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            businessTypeCode: 'businessTypeCode',
            identifyCard: 'identifyCard'
        }),
        ...mapState('temporaryCardHandle', {
            result: 'result',
            personalInfo: 'personalInfo',
            orderDetail: 'orderDetail'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'setSteps', 'changeTipPage']),
        ...mapMutations('temporaryCardHandle', ['setPersonalInfo']),
        async informationQuery () {
            const res = await processInformationQuery();
            if (res && res.code === '0') {
                this.setPersonalInfo({ ...res.result.data, cbr: res.result.cbr });
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message ? res.message : res.result.msg, enableBack: true });
            }
        },
        async checkPayStatus () {
            const res = await checkDriverPayStatus();
            if (res && res.code === '0') {
                this.changeStep('down');
            }
        },
        async tickPrint () {
            const data = {
                ...this.result,
                ...this.orderDetail,
                ...this.personalInfo,
                certNumber: this.identifyCard.certNumber,
                partyName: this.identifyCard.partyName
            };
            const res = await tickPrint({ content: getIdNumberTick(data) });
            this.changeStep('down');
            console.log('打印凭条结果：', res);
        }
    },
    beforeMount () {
        this.setSteps(ID_NUMBER_TEMP);
    }
};
</script>

